.footer{
    width:100%;
    height: 25vh;
    background-color:#EBFFE4;
}
.footerContainer {
    border: 1px solid;
    margin: 0 auto;
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
}
.footerEmptyContainer{
    width:50%;
    border: 1px solid;
    display: flex;
    flex-direction:row;
    align-items: center;
    justify-content: space-between;
}
.insideEmpty{
    height:70px;
    width:15%;
    background-color:#cabebe;

}
